                        <div class="lc-chat__list">
                            <ThreadItem v-for="(item) in threads"
                                        :thread="item"
                                        @click="messenger.threads.setThread(item)"
                                         :key="item.id">
                            </ThreadItem>
                        </div>
                    </div>
                </div>

                <div class="ui-col-sm-9 hidden-sm">
                    <div class="wrapper">
